import { useState } from '@wordpress/element';
import { useBlockProps, RichText, InspectorControls, MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { Flex, FlexBlock, ToggleControl, Button } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

import blockImage from '../../../../admin/assets/img/blocks/block-11.jpg';

import PictureBgEdit from '../../components/PictureBgEdit';
import CF7FormSelector from '../../components/CF7FormSelector';

import VideoHelpPanel from './controls/VideoHelpPanel';
import ContentPanel from './controls/ContentPanel';
import BgAnchorPanel from './controls/BgAnchorPanel';

const Edit = ({ attributes, setAttributes }) => {
  const { title, subTitleOne, subTitleTwo, divider, descr, imageId, imageData } = attributes;

  const [isPreview, setIsPreview] = useState(false);

  const blockProps = useBlockProps({
    className: 'block-style block-11'
  });

  // Handler - image
  const onSelectImage = (media) => {
    setAttributes({
      imageId: media.id,
      imageData: {
        url: media.url,
        alt: media.alt || '',
        responsive: media.responsive || {
          webp: '',
          jpg: '',
          default: media.url,
        }
      }
    });
  };

  const onRemoveImage = () => {
    setAttributes({
      imageId: 0,
      imageData: {
        url: '',
        alt: '',
        responsive: {
          webp: '',
          jpg: '',
          default: '',
        }
      }
    });
  };

  // Handler - bg
  const bgSizes = [1920, 991, 576];
  const getOnSelectBg = (size) => (media) => {
    setAttributes({
      [`bg${size}Id`]: media.id,
      [`bg${size}Data`]: {
        url: media.url,
        alt: media.alt || '',
        responsive: media.responsive || {
          webp: '',
          jpg: '',
          default: media.url,
        },
      },
    });
  };

  const getOnRemoveBg = (size) => () => {
    setAttributes({
      [`bg${size}Id`]: 0,
      [`bg${size}Data`]: {
        url: '',
        alt: '',
        responsive: {
          webp: '',
          jpg: '',
          default: '',
        },
      },
    });
  };

  const togglePreview = () => {
    setIsPreview(!isPreview);
  };

  return (
    <>
      <InspectorControls>
        <VideoHelpPanel />
        <ContentPanel attributes={attributes} setAttributes={setAttributes} />
        <BgAnchorPanel attributes={attributes} setAttributes={setAttributes} />
      </InspectorControls>

      <div {...blockProps}>
        <div className="advanced-block">
          <div className="block-info">
            <span className="block-info-title"> Block 11 -    </span>
            <ToggleControl
              label={isPreview ? __(' ', 'theme') : __(' ', 'theme')}
              checked={isPreview}
              onChange={togglePreview}
            />
          </div>

          {!isPreview && (
            <img src={blockImage} alt="MGUBS" style={{ width: '100%', height: 'inherit', objectFit: 'contain' }} />
          )}

          {isPreview && (
            <div className="advanced-block-content">
              <Flex direction={['column', 'row']} align="flex-start">
                <FlexBlock>
                  <>
                    <span>{__('', 'theme')}</span>
                    <RichText
                      tagName="div"
                      value={title}
                      onChange={(value) => setAttributes({ title: value })}
                      placeholder={__('...', 'theme')}
                      allowedFormats={[]}
                    />
                  </>
                  <>
                    <span>{__(' 1', 'theme')}</span>
                    <RichText
                      tagName="div"
                      value={subTitleOne}
                      onChange={(value) => setAttributes({ subTitleOne: value })}
                      placeholder={__('', 'theme')}
                      allowedFormats={['core/bold', 'core/italic', 'core/link']}
                    />
                  </>
                </FlexBlock>
                <FlexBlock>
                  <MediaUploadCheck>
                    <MediaUpload
                      onSelect={onSelectImage}
                      allowedTypes={['image']}
                      value={imageId}
                      render={({ open }) => (
                        <div className="advanced-block-image advanced-block-image-100">
                          <div className="label-image"> ( )</div>
                          {imageData.url ? (
                            <>
                              <img
                                src={imageData.url}
                                className="advanced-image-preview"
                                alt=""
                                style={{ borderRadius: '8px' }}
                              />
                              <div style={{ display: 'flex', gap: '6px', marginTop: '4px' }}>
                                <Button onClick={open} variant="secondary" size="small">
                                   {__('', 'theme')}
                                </Button>
                                <Button
                                  onClick={onRemoveImage}
                                  variant="tertiary"
                                  size="small"
                                  isDestructive
                                >
                                   {__('', 'theme')}
                                </Button>
                              </div>
                            </>
                          ) : (
                            <Button onClick={open} variant="primary">
                               {__(' ', 'theme')}
                            </Button>
                          )}
                        </div>
                      )}
                    />
                  </MediaUploadCheck>
                </FlexBlock>
              </Flex>

              <ToggleControl
                label={divider ? __('  ', 'theme') : __('  ', 'theme')}
                checked={divider}
                onChange={(value) => setAttributes({ divider: value })}
              />

              <Flex direction={['column', 'row']}>
                <FlexBlock>
                  <span>{__(' 2', 'theme')}</span>
                  <RichText
                    tagName="div"
                    value={subTitleTwo}
                    onChange={(value) => setAttributes({ subTitleTwo: value })}
                    placeholder={__('', 'theme')}
                    allowedFormats={['core/bold', 'core/italic', 'core/link']}
                  />
                </FlexBlock>
              </Flex>

              <Flex direction={['column', 'row']}>
                <FlexBlock>
                  <span>{__('', 'theme')}</span>
                  <RichText
                    tagName="div"
                    value={descr}
                    onChange={(value) => setAttributes({ descr: value })}
                    placeholder={__('...', 'theme')}
                    allowedFormats={['core/bold', 'core/italic', 'core/link', 'core/underline', 'core/text-color']}
                  />
                </FlexBlock>
              </Flex>

              <Flex direction={['column', 'row']}>
                <FlexBlock>
                  <CF7FormSelector attributes={attributes} setAttributes={setAttributes} />
                </FlexBlock>
              </Flex>

              <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr', rowGap: '16px', columnGap: '16px', width: '100%' }}>
                {bgSizes.map((size) => (
                  <PictureBgEdit
                    key={size}
                    label={` ${size}px`}
                    imageId={attributes[`bg${size}Id`]}
                    imageData={attributes[`bg${size}Data`]}
                    onSelect={getOnSelectBg(size)}
                    onRemove={getOnRemoveBg(size)}
                  />
                ))}
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default Edit;